<template>
  <nav class="navbar navbar-expand-lg navbar-light ">
  <a class="navbar-brand" href="https://github.com/fatihunlu"> 
    <img src="/static/github.ico" width="30" height="30" class="d-inline-block align-top" alt="">
    BioInformaticTools
  </a>
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav mr-auto">
        <li class="nav-item" :class="{ 'active' : selectedTabIndex === 1 }">
          <router-link class="nav-link" :to="{ name: 'HomePage' }" v-on:click.native="changeTabIndex(1)">首页</router-link>
        </li>
        <li class="nav-item" :class="{ 'active' : selectedTabIndex === 2 }">
          <router-link class="nav-link" :to="{ name: 'AboutPage' }"  v-on:click.native="changeTabIndex(2)"> 资源 </router-link>
        </li>
        <li class="nav-item" :class="{ 'active' : selectedTabIndex === 3 }">
          <router-link class="nav-link" :to="{ name: 'ServicesPage' }" v-on:click.native="changeTabIndex(3)"> 学习路线 </router-link>
        </li>
        <li class="nav-item" :class="{ 'active' : selectedTabIndex === 4 }">
          <router-link class="nav-link " :to="{ name: 'ContactPage' }" v-on:click.native="changeTabIndex(4)"> 联系我们 </router-link>
        </li>
      </ul>
      <form class="form-inline my-2 my-lg-0" v-if="showSearchbar">
        <input class="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search">
        <button class="btn btn-primary my-2 my-sm-0 disabled" type="submit" @click.prevent=";">Search</button>
      </form>
    </div>
  </nav>
</template>

<script>


export default {
    props: {
      showSearchbar: {
        requried: false,
        default: true,
        type: Boolean
      }
    },
    data() {
        return {
          selectedTabIndex : 1
        }
    },
    methods: {
      changeTabIndex(index) {
        const vm = this;

        vm.selectedTabIndex = index;
      }
    }
}
</script>

<style>
  nav {
    font-size: 26px;
    font-weight: bold;
    width: 100%;
    background-color: rgb(203, 204, 206);
    position: fixed;
    top:0;


  }
  .nav-item{
    margin-right:30px ;
    margin-left: 10px;
  }
</style>


